
.thumb[data-v-ea115f84] {
  -webkit-transition: top .2s, height .2s, width .2s, margin-left .2s;
  transition: top .2s, height .2s, width .2s, margin-left .2s;
}
input[type="range"][data-v-ea115f84] {
  -webkit-appearance: none;
}

/* thumb */
input[type=range][data-v-ea115f84]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 14px;
  width: 14px;
  border-radius: 50%;
  background-color: #4285f4;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  margin: -5px 0 0 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
input[type=range][data-v-ea115f84]:focus::-webkit-slider-runnable-track {
    background: #ccc;
}
input[type=range][data-v-ea115f84]::-moz-range-track {
    /*required for proper track sizing in FF*/
    height: 3px;
    background: #c2c0c2;
    border: none;
}
input[type=range][data-v-ea115f84]::-moz-range-thumb {
    border: none;
    height: 14px;
    width: 14px;
    border-radius: 50%;
    background: #4285f4;
    margin-top: -5px;
}
input[type=range][data-v-ea115f84]:-moz-focusring {
    /*hide the outline behind the border*/
    outline: 1px solid #ffffff;
    outline-offset: -1px;
}
input[type=range][data-v-ea115f84]:focus::-moz-range-track {
    background: #c2c0c2;
}
input[type=range][data-v-ea115f84]::-ms-track {
    height: 3px;
    background: transparent;
    /*remove bg colour from the track, we'll use ms-fill-lower and ms-fill-upper instead */
    border-color: transparent;
    /*leave room for the larger thumb to overflow with a transparent border */
    border-width: 6px 0;
    color: transparent;
    /*remove default tick marks*/
}
input[type=range][data-v-ea115f84]::-ms-fill-lower {
    background: #c2c0c2;
}
input[type=range][data-v-ea115f84]::-ms-fill-upper {
    background: #c2c0c2;
}
input[type=range][data-v-ea115f84]::-ms-thumb {
    border: none;
    height: 14px;
    width: 14px;
    border-radius: 50%;
    background: #4285f4;
}
input[type=range][data-v-ea115f84]:focus::-ms-fill-lower {
